import React, { Component } from 'react'
import { Button, Container } from 'reactstrap'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import '../../css/client/Banner.css'

class Banner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            keyword: ''
        }
    }

    handeChange = (e) => {
        let target = e.target
        let name = target.name
        let value = target.value
        this.setState({
            [name]: value
        })
    }
    handleOnclick = () => {
        let { keyword } = this.state
        this.props.onFindProduct(keyword)
        this.setState({
            keyword: ''
        })
    }
    render() {
        return (
            <div className="Banner">
                <Container>
                    <div className="title">
                        <h1>Ca hng sch ca ring bn</h1>
                        <p>Nhn nhng cun sch yu thch ca bn t nhiu loi sch ca chng ti.</p>
                    </div>
                    <div className="search-bar">
                        <span className="label">Sch</span>
                        <form>
                            <input className="p-0" type="text" placeholder="Tm kin sch ca bn ti y" name="keyword" value={this.state.keyword} onChange={this.handeChange} />
                        </form>
                        <Button onClick={this.handleOnclick}>
                            <FontAwesomeIcon icon={faSearch} className="mr-2" />
                            Tm kim
                        </Button>
                    </div>
                </Container>
            </div>
        )
    }
}


export default Banner