import React, { Component } from 'react'
import Nav from '../../components/client/Nav'
import { Button } from 'reactstrap'
import { Link } from 'react-router-dom'
import '../../css/client/OrderReceived.css'
import { connect } from 'react-redux'

class OrderReceived extends Component {

    totalQuantity = (cart) => {
        let totalQuantity = null
        cart.forEach(element => {
            totalQuantity += element.quantity
        });
        return totalQuantity
    }
    render() {
        let { order } = this.props
        console.log(order)
        let { match } = this.props.match
        let id = match.params.id
        let orderCurrently = order.find(element => element._id === id)
        console.log(orderCurrently)
        return (
            <div>
                <Nav />
                <div className="order-received user-container">
                    <div className="user-wrapper">
                        <div className="btn-back-to-home">
                            <Button>
                                <Link to="/">Tr li</Link>
                            </Button>
                        </div>
                        <div className="received mb-5">
                            <h3 className="bt-header">n hng  c nhn</h3>
                            <p className="mb-4">Cm n bn n t hng ca bn  c nhn</p>
                            <div className="info">
                                <div className="info-data">
                                    <div className="info-header">M n hng</div>
                                    <p>{orderCurrently._id}</p>
                                </div>
                                <div className="info-data">
                                    <div className="info-header">Ngy</div>
                                    <p>{orderCurrently.date}</p>
                                </div>
                                <div className="info-data">
                                    <div className="info-header">Tng tin</div>
                                    <p>${orderCurrently.totalPrice}</p>
                                </div>
                                <div className="info-data">
                                    <div className="info-header">Phng thc thanh ton</div>
                                    <p>{orderCurrently.payment === 'cash' ? 'Tin mt' : 'Th ATM'}</p>
                                </div>
                            </div>
                        </div>
                        <div className="detail mb-5">
                            <h3 className="bt-header">Chi tit n hng</h3>
                            <div className="detail-info">
                                <div className="info-header m-0">Sn phm</div>
                                <p>{this.totalQuantity(orderCurrently.cartItem)}{this.totalQuantity(orderCurrently.cartItem) < 2 ? "Item" : "Items"}</p>
                            </div>
                            <div className="detail-info">
                                <div className="info-header m-0">Thi gian t hng</div>
                                <p>{orderCurrently.date}</p>
                            </div>
                            <div className="detail-info">
                                <div className="info-header m-0">a ch</div>
                                <p>{orderCurrently.address}</p>
                            </div>
                        </div>
                        <div className="amount detail">
                            <h3 className="bt-header">Tng cng</h3>
                            <div className="detail-info">
                                <div className="info-header m-0">Thnh tin</div>
                                <p>${orderCurrently.totalPrice}</p>
                            </div>
                            <div className="detail-info">
                                <div className="info-header m-0">Phng thc thanh ton</div>
                                <p>{orderCurrently.payment === 'cash' ? 'Tin mt' : 'Th ATM'}</p>
                            </div>
                            <div className="detail-info">
                                <div className="info-header m-0">Tng tin</div>
                                <p>${orderCurrently.totalPrice}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        order: state.order
    }
}


export default connect(mapStateToProps, null)(OrderReceived)